
















import { OLabel } from '../label';
import Vue from 'vue';

export default Vue.extend({
  name: 'OInput',
  components: { OLabel },
  model: {
    prop: 'value',
    event: 'input',
  },
  props: ['id', 'value'],
  computed: {
    vModel: {
      get(): unknown {
        return this.value;
      },
      set(value: unknown): void {
        this.$emit('input', value);
      },
    },
  },
});
